import React, { Component } from 'react';
import logo from './logo.svg';
import SignIn from './SignIn';
import Churches from './Churches';
import Responses from './Responses';
import TabMenu from './TabMenu';
import './App.css';
import { adminAuthUid } from './constants'
import firebase from 'firebase'

var config = {
  apiKey: "AIzaSyAlQqNRvE3_6gGG7n4Lex5QftQoZ076ygk",
  authDomain: "unstuck-app.firebaseapp.com",
  databaseURL: "https://unstuck-app.firebaseio.com",
  projectId: "unstuck-app",
  storageBucket: "unstuck-app.appspot.com",
  messagingSenderId: "516397577712"
};
firebase.initializeApp(config);
var db = firebase.database();


class App extends Component {
  componentDidMount(){
    this.setState({
      auth: null,
      churches: null,
      responses: null,
      currentTab: "churches"
    })

    firebase.auth().onAuthStateChanged((user)=>{
      if (user) {
        this.setState({ auth: user })
        db.ref('church').once('value')
        .then((churchSnapshot) => 
          this.setState({ churches: churchSnapshot.val() })
        )
        db.ref('response').once('value')
        .then((responseSnapshot) =>
          this.setState({ responses: responseSnapshot.val() })
        )
      } else {
        this.setState({ auth: null })
      }
    })
  }
  signOut(){
    firebase.auth().signOut()
  }
  signIn(email, password){
    firebase.auth().signInWithEmailAndPassword(email, password)
    .catch(function(error) {
      //var errorCode = error.code;
      //var errorMessage = error.message;
    })
  }
  setTab(tabName){
    this.setState({ currentTab: tabName })
  }
  render() {
    if(!this.state || !this.state.auth){
      return (<SignIn onSignIn={this.signIn} />)
    } 
    if(this.state.auth && this.state.auth.uid === adminAuthUid){
      return (
        <div className="App">
          <div className="App-header">
            <div className="container" style={{
              display: "flex",
              justifyContent: "space-between"
            }}>
              <img src={logo} className="App-logo" alt="logo" />  
              <p style={{ cursor: "pointer", marginTop: 7, fontWeight: 400 }} onClick={this.signOut}>Logout</p>
            </div>
          </div>
          <div className="App-intro container">
            <TabMenu setTab={this.setTab.bind(this)} tab={this.state.currentTab}/>
            
            { this.state.currentTab === "churches" ? 
              <Churches churches={this.state.churches} /> : 
              <Responses responses={this.state.responses} />}
          </div>
        </div>
      );
    }

    return (<div style={{ 
        textAlign: "center",
        paddingTop: 100
      }}>
        <h4>This user is not an admin.</h4>
        <p onClick={this.signOut}>Logout</p>
      </div>)
  }
}

export default App;



