import React, { Component } from 'react';

const itemStyle = {
	marginRight: 20,
	cursor: "pointer"
}

const selectStyle = {
	marginRight: 20,
	fontWeight: 600,
	cursor: "pointer"
}

class TabMenu extends Component {
  render() {
    return (
		<p style={{ paddingTop: 15, fontSize: 15 }}>
          <span 
          	style={this.props.tab === "churches" ? selectStyle : itemStyle} 
          	onClick={()=>this.props.setTab("churches")}>
          	Churches
          </span>
          <span 
          	style={this.props.tab === "responses" ? selectStyle : itemStyle} 
          	onClick={()=>this.props.setTab("responses")}>
          	Individuals
          </span>
        </p>
      )
  }
}


export default TabMenu;




