import React, { Component } from 'react';

const Response = ({index, text, average}) => {
  return (
    <div className="response row" style={{ padding: 5}}>
      <p style={{ maxWidth: 400 }} >{index}. {text}</p>
      <p><span style={{ opacity: .7, paddingRight: 10 }}>Average response: </span>{(Math.round(average * 10) / 10).toFixed(1)} / 10</p>
    </div>
  )
}

export default Response


