import React, { Component } from 'react';
import moment from 'moment';
import { phases, getPhaseId, opinionQuestions } from './constants'
import Response from './Response'

class ResponseItem extends Component {
componentDidMount(){
    this.setState({
      expanded: false
    })
  }
  render(){
    if(!this.state) { return null; }
    var data = this.props.data
    var questionData = data.surveyData.questions
    var numResponses = 1
    return (
    	<div className="card">
    		<div className="row">
    			<p>{data.email}</p>
          <p>Result: {phases[getPhaseId(data.surveyData.questions) + 1]} </p>

    			<p className="small">{moment(data.surveyData.timeSubmitted).fromNow()}</p>
    		
           

        </div>
         { !this.state.expanded ? 
              (<p style={{ cursor: "pointer", textDecoration: "underline", fontSize: 11 }} onClick={()=>this.setState({expanded: true})}>View Responses</p>) : 
              (
                <div>
                  <p style={{ cursor: "pointer", paddingBottom: 10, textDecoration: "underline", fontSize: 11 }}  onClick={()=>this.setState({expanded: false})}>Hide Responses</p>
                  {Object.keys(questionData).map(x =>
                        <Response key={x} index={x - 0 + 1} text={opinionQuestions[x] ? opinionQuestions[x].title : ""} average={questionData[x].value / numResponses}/>
                      )}
                </div>
              ) 
            }
    	</div>
    );
  }
}


class Responses extends Component {
  componentDidMount(){
    this.setState({
      expanded: false
    })
  }
  render() {
  	if(!this.props.responses || !this.state) { return null }

    return (
      <div>
        { Object.keys(this.props.responses).map(x => 
        	<ResponseItem key={x} data={this.props.responses[x]}/>
    	)}
      </div>
    )
  }
}

export default Responses;


