import React, { Component } from 'react';
import moment from 'moment';
import { opinionQuestions, denominations, regions, phases, getChurchPhase, getPhaseId, averageResponse } from './constants'
import Response from './Response'

class Church extends Component {
	componentDidMount(){
		this.setState({
			expanded: false
		})
	}
	render() {
		var data = this.props.data
		var questionData = averageResponse(data.responses).surveyData.questions
		var numResponses = Object.keys(data.responses).length
		var phase = getChurchPhase(data.churchData, questionData)
		var opinionPhase = 1 + Number(getPhaseId(questionData))
		console.log(opinionPhase, questionData)
		//debugger
		if(!this.state) { return null }
		return (
			<div className="card" id={this.props.id}>
				<div className="row">
					<p style={{ fontWeight: 600 }}>{data.churchData.name}</p>
					<p className="small">{moment(data.churchData.timeCreated).fromNow()}</p>
				</div>
				<p>Leader: {data.responses.initial.email}</p>
				<p>Result: {phases[phase.stage + 1]} </p>
				<div className="row left">
					<p className="small">Denomination: {denominations[data.churchData.denomination]}</p>
					<p className="small">Region: {regions[data.churchData.region]}</p>
					<p className="small">Opinion-only Result: {phases[opinionPhase]}</p>
				</div>
				<p className="small">Attendance (last year): {data.churchData.attendanceOne} / (two years ago): {data.churchData.attendanceTwo} / (three years ago): {data.churchData.attendanceThree}</p>
				<p className="small">Giving (last year): {data.churchData.givingOne} / (two years ago): {data.churchData.givingTwo} / (three years ago): {data.churchData.givingThree}</p>
				<p className="small">Conversions (past year): {data.churchData.conversions}</p>
				<p className="small">Team response emails:</p>
				{ 
					Object.keys(data.responses).map(x => 
						x !== "initial" ? <p key={x} className="small">{data.responses[x].email}</p> : null
					)
				}
				<br/>
				{ !this.state.expanded ? 
					(<p style={{ cursor: "pointer", textDecoration: "underline", fontSize: 11 }} onClick={()=>this.setState({expanded: true})}>View Responses</p>) : 
					(
						<div>
							<p style={{ cursor: "pointer", paddingBottom: 10, textDecoration: "underline", fontSize: 11 }}  onClick={()=>this.setState({expanded: false})}>Hide Responses</p>
							{Object.keys(questionData).map(x =>
					          <Response key={x} index={x - 0 + 1} text={opinionQuestions[x] ? opinionQuestions[x].title : ""} average={questionData[x].value / numResponses}/>
					        )}
						</div>
					) 
				}
			</div>
		)
	}
}


class Churches extends Component {
  render() {
  	if(!this.props.churches) { return null }

    return (
      <div>
        { Object.keys(this.props.churches)
        	.sort((a,b) => this.props.churches[b].churchData.timeCreated 
        		- this.props.churches[a].churchData.timeCreated 
    		)
        	.map(x => <Church key={x} id={x} data={this.props.churches[x]}/>) }
      </div>
    )
  }
}

export default Churches;



