exports.getPhaseId = function(questions) {
  const stages = questions
    .reduce((data, value, index) => {
      const stage = getStageForIndex(index)
      if (data[stage]) {
        data[stage].value += value.value || 0
      } else {
        data.push({ stage, value: value.value })
      }
      return data
    }, [])
  var stagesForSide = stages.slice(0)
  return stagesForSide.sort((a, b) => b.value - a.value)[0].stage
}

function getStageForIndex (index) {
  return Math.floor(index / 7)
}

function getTopStage (sortedQuestions, side) {
  const stages = sortedQuestions
    .reduce((data, value, index) => {
      const stage = getStageForIndex(index)
      if (data[stage]) {
        data[stage].value += value.value || 0
      } else {
        data.push({ stage, value: value.value })
      }
      return data
    }, [])
  let stagesForSide = []

  if (side === 'left') {
    stagesForSide = stages.slice(0, 4)
  } else if (side === 'right') {
    stagesForSide = stages.slice(4)
  } else {
    stagesForSide = stages.slice(0)
  }
  return stagesForSide.sort((a, b) => b.value - a.value)[0]
}

function getPercentConversionsGrowth (churchData) {
  return Number(churchData.conversions) / Number(churchData.attendanceOne)
}

function getAttendanceGrowth(churchData){
  return Number(churchData.attendanceOne) - Number(churchData.attendanceThree)
}

exports.getChurchPhase = function(churchData, responses) {
  return getTopStage(responses, getLifecycleSide(churchData))
}

function  getLifecycleSide (churchData) {
  if (churchData) {
    //console.log(getAttendanceGrowth(churchData), getPercentConversionsGrowth(churchData))
    if (getAttendanceGrowth(churchData) > 0 && getPercentConversionsGrowth(churchData) > 0.06) {
      return 'left'
    }
    return 'right'
  }
}

exports.averageResponse = function(responses){
	var numResponses = Object.keys(responses).length
	var averageResponse = responses.initial
	var qs = averageResponse.surveyData.questions

	qs.forEach(x => x.value = x.value || 0)
	Object.values(responses).forEach(r => {
	  if(r === responses.initial) return
	  r.surveyData.questions.forEach((q, i) => {
	    qs[i].value += (q.value || 0)
	  })
	})
	qs.forEach(q => q.value /= numResponses)
	return averageResponse
}

